	module.exports = {"data":{"markdownRemark":{"html":"<h4>나는 희망을 거절한다</h4>\n<div style=\"text-align: right\">정호승</div>\n<p>나는 희망이 없는 희망을 거절한다</p>\n<p>희망에는 희망이 없다</p>\n<p>희망은 기쁨보다 분노에 가깝다</p>\n<p>나는 절망을 통하여 희망을 가졌을 뿐</p>\n<p>희망을 통하여 희망을 가져본 적이 없다</p>\n<br>\n<p>나는 절망이 없는 희망을 거절한다</p>\n<p>희망은 절망이 있기 때문에 희망이다</p>\n<p>희망만 있는 희망은 희망이 없다</p>\n<p>희망은 희망의 손을 먼저 잡는 것보다</p>\n<p>절망의 손을 먼저 잡는 것이 중요하다</p>\n<br>\n<p>희망에는 절망이 있다</p>\n<p>나는 희망의 절망을 먼저 원한다</p>\n<p>희망이 절망이 절망이 될 때보다</p>\n<p>희망의 절망이 희망이 될 때</p>\n<p>당신을 사랑한다</p>\n<hr>\n<p>나는 정말 희망만 있는 희망은 희망이라고 생각하지 않았다.</p>\n<p>친숙했던 희망이 다시한번 낯설게 다가온다.</p>","frontmatter":{"title":"나는 희망을 거절한다, 정호승"}}},"pathContext":{"slug":"/posts/2017-06-05-deny-hope/"}}

